@import "@theme/vars.scss";

.audiobadge {
    padding-top: 60px;
    padding-bottom: 75px;

    @media only screen and (max-width: $bp-xl) {
        padding-bottom: 50px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding-top: 50px;
        padding-bottom: 40px;
    }

    @media only screen and (max-width: $bp-md) {
        padding-top: 30px;
        padding-bottom: 25px;
    }

    
    &__inner {
    }

    &__intro {
        margin-bottom: 140px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 100px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 75px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 50px;
        }
    }
    
    &__info {
        margin-bottom: 70px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 50px;
        }
    }

    &__list {
        // display: none;
    }
}

.audiobadge-intro {
    color: #fff;

    &__inner{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 492px;
    }

    &__title-wrapper {
        width: 100%;
        max-width: 690px;
        margin-bottom: 48px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
        }
    }

    &__subtitle {
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 0.39em;
        color: $blue;
        font-size: 18px;
        line-height: 145%;
        margin-bottom: 18px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }
    }

    &__title {

        @media only screen and (max-width: $bp-xl) {
            font-size: 26px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-sm) {
            max-width: 286px;
        }
    }

    &__content {
        width: 100%;
        max-width: 522px;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
        }

        p{
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 10px;

            span{
                font-weight: 700;
                color: $blue;
            }

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }

    &__profile {
        box-sizing: border-box;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        margin: auto 0;
        display: flex;
        flex-direction: column;
        width: 392px;
        height: 492px;
        padding: 36px 36px 62px 36px;
        flex-shrink: 0;
        border-radius: 18px;
        overflow: hidden;
        border: 1px solid $blue;

        @media only screen and (max-width: $bp-lg) {
            position: relative;
            margin: 0 0 30px;
            padding: 30px 30px 52px;
            height: 420px;
            width: 335px;
        }

        @media only screen and (max-width: $bp-sm) {
            margin-right: auto;
            margin-left: auto;
        }

        &::after{
            content: "";
            display: block;
            width: 100%;
            height: 50%;
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;
            background: linear-gradient(181.57deg, rgba(45, 44, 44, 0) 1.3%, rgba(34, 33, 33, 0.79) 57.08%, #1A1919 98.67%);
        }
    }

    &__profile-inner {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__profile-logo {
        display: block;
        flex-shrink: 0;
        width: 70px;
        height: 48px;

        @media only screen and (max-width: $bp-lg) {
            width: 60px;
            height: 41px;
        }
    }

    &__profile-name {
        margin-top: auto;
        margin-bottom: 18px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
            font-size: 30px;
        }
    }

    &__profile-subtitle {
        color: #636061;
    }

    &__profile-img{
        display: flex;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.audiobadge-info {

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
        }
    }

    &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 675px;
        height: 402px;
        margin-left: 35px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xxl) {
            width: 550px;
            height: 328px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 420px;
            height: 250px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: calc(100% - 30px);
            max-width: 540px;
            max-height: 422px;
            height: auto;
            order: -1;
            margin-bottom: 50px;
            margin-left: 0;
        }

        img{
            width: 100%;
            height: 154%;
            margin-bottom: -20%;
            margin-top: -12%;
            object-fit: contain;

            @media only screen and (max-width: $bp-lg) {
                width: 124%;
                height: 112%;
                margin-bottom: -16%;
            }
        }
    }

    &__item-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
        }

        p{
            color: #F0F0F0;
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 15px;
            
            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }

            &:last-of-type{
                margin-bottom: 25px;

                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 20px;
                }
            }
        }
    }

    &__button {

        @media only screen and (max-width: $bp-lg) {
            margin: 0 auto;
        }
    }
}

.audiobadge-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px -20px;

    @media only screen and (max-width: $bp-md) {
        margin: 0 0 -20px;
    }

    &__item {
        box-sizing: border-box;
        width: 33.33%;
        padding: 0 16px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            text-align: center;
            width: 50%;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            padding: 0;
        }
    }

    &__img {
        display: flex;
        width: 100%;
        margin-bottom: 16px;
        flex-shrink: 0;
        aspect-ratio: 1.45;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 5px;
            max-width: 325px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: 275px;
        }

        img{
            height: 100%;
            width: 100%;
            object-fit: contain;
        }
    }

    p {
        font-family: 'DelaGothicOne';
        font-size: 16px;
        line-height: 135%;
        color: #D9D9D9;

        @media only screen and (max-width: $bp-xl) {
            font-size: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: 350px;
        }


        span{
            color: $blue;
        }
    }
}
