@import "@theme/vars.scss";

.input{
    position: relative;
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    width: 100%;
    font-family: 'NotoSansMono';
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    transition: padding 0.35s ease;

    @media only screen and (max-width: $bp-md) {
        font-size: 14px;
    }

    &__field{
        outline: none;
        box-sizing: border-box;
        width: 100%;
        height: 55px;
        padding: 0 35px;
        background-color: transparent;
        color: #fff;
        border: 1px solid rgba(255, 255, 255, 0.6);
        border-radius: 30px;
        transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 46px;
            padding: 0 32px;
        }

        // &:focus{
		//     border-color: $blue;
        // }

        &::placeholder{
            color: #fff;
            opacity: 0.6;
        }
    }

    &__error{
        color: #FF0000;
        font-size: 12px;
        line-height: 14px;
        visibility: hidden;
        opacity: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        max-width: 100%;
        transition: transform 0.35s ease, opacity 0.35s ease, visiblity 0.35s ease;
    }


    &.is-error{
        padding-bottom: 20px;
    }

    &.is-error &__field{
        border-color: #FF0000;

        &::placeholder{
            color: #FF0000;
        }
    }

    &.is-error &__error{
        opacity: 1;
        visibility: visible;
    }

    &.is-filled &__field{
        border-color: #fff;
    }

	&.is-focus &__field{
        border-color: #fff;
	}

    
    &--blue &__field{
        border-color: rgba(48, 128, 226, 0.6);
        color: $blue;

        &::placeholder{
            color: $blue;
        }
    }

    &--blue.is-filled &__field{
        border-color: $blue;
    }

	&--blue.is-focus &__field{
        border-color: $blue;
	}

}