@import '@theme/vars.scss';

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

p{
    font-family: 'NotoSansMono';
    font-weight: 400;
}


.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: 'DelaGothicOne';
    font-weight: 400;
}

.h1{
    font-size: 45px;
    line-height: 125%;
    
    @media only screen and (max-width: $bp-xl) {
        font-size: 40px;
    }

    @media only screen and (max-width: $bp-lg) {
        font-size: 35px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 30px;
    }
}

.h2{
    font-size: 35px;
    line-height: 125%;

    @media only screen and (max-width: $bp-xl) {
        font-size: 30px;
    }

    @media only screen and (max-width: $bp-lg) {
        font-size: 26px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 22px;
    }
}

.h3{
    font-size: 25px;
    line-height: 125%;

    @media only screen and (max-width: $bp-xl) {
        font-size: 22px;
    }

    @media only screen and (max-width: $bp-lg) {
        font-size: 20px;
    }
}

.h4{
    font-size: 20px;
    line-height: 125%;

}

.h5{
   
}

.text{
    font-size: 16px;
    line-height: 145%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 14px;
    }

    &-sm{
        font-size: 12px;
        line-height: 145%;
    }

    &-md{
        font-size: 14px;
        line-height: 145%;
    }

    &-lg{
        font-size: 18px;
        line-height: 145%;
    }

    &-xl{
        font-size: 20px;
        line-height: 145%;
    }

    //STYLE

    // &-border{
    //     -webkit-text-stroke: 1px #8C8C8C;
    // }

    //WEIGHT

    &-400{
        font-weight: 400;
    }
    
    &-500{
        font-weight: 500;
    }

    &-600{
        font-weight: 500;
    }

    &-700{
        font-weight: 700;
    }

    //FAMILY

    &-noto{
        font-family: 'NotoSansMono';
    }

    &-dela{
        font-family: 'DelaGothicOne';
    }
}