@import "@theme/vars.scss";

.analytics {
    padding-bottom: 0 !important;

    &__inner{
        padding: 0 20px;

        @media only screen and (max-width: $bp-xl) {
            padding: 0;
        }
    }

    &__intro {
        margin-bottom: 30px;
    }

    &__list {
    }

    &__intro-img{
        pointer-events: none;
        display: flex;
        justify-content: center;
        width: 100%;
        max-width: 1440px;
        height: 698px;
        margin: -200px auto 0;

        @media only screen and (max-width: $bp-md) {
            margin-top: -270px;
            height: 520px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: bottom center;

            @media only screen and (max-width: $bp-md) {
                object-position: 66%;
            }
        }
    }
}

.analytics-block {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding: 65px;
    min-height: 350px;
    background: linear-gradient(136.09deg, #307FE2 -28.54%, #0857BA 100%);
    color: #fff;
    border: 2px solid #FFFFFF;
    box-shadow: 12px 13px 35px rgba(56, 124, 202, 0.3);
    border-radius: 15px;
    overflow: hidden;

    @media only screen and (max-width: $bp-xxl) {
        padding: 32px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 24px 32px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 18px;
    }
    
    &__inner{
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        flex-grow: 1;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            padding-bottom: 73px;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 500px;
        flex-shrink: 0;
        margin-right: 60px;

        @media only screen and (max-width: $bp-xl) {
            max-width: 450px;
            margin-right: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            margin-bottom: 25px;
            margin-right: 0;
        }
    }

    &__main-img {
        display: block;
        width: 48px;
        height: 48px;
        margin-bottom: 15px;
        flex-shrink: 0;
        filter: drop-shadow(0px 11px 31px #0350B3);
    }

    &__main-title {
        margin-bottom: 10px;
    }

    &__main-text {
        color: #F0F0F0;
        margin-bottom: 10px;
    }

    &__main-subtitle {
        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__button{
        height: 48px;
        margin-top: 15px;
        border-radius: 10px;
        padding: 0 20px;

        @media only screen and (max-width: $bp-lg) {
            position: absolute;
            bottom: 0;
            left: 0;

        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }
  
    &__info {
        width: 100%;
        max-width: 492px;
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-lg) {
            align-items: flex-start;
            max-width: none;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px 5px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 0 5px;
            flex-direction: column;
        }
    }

    &__list-item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        padding: 0 15px;
        margin-bottom: 20px;
        max-width: 300px;

        @media only screen and (max-width: $bp-xxl) {
            max-width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            width: 100%;
            padding: 0;
        }
    }

    &__list-title {
        font-family: 'NotoSansMono';
        font-weight: 600;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            margin-right: 10px;
        }

        span{
            margin-left: 10px;
        }
    }

    &__list-text {
    }

    &__item {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        border-radius: 14px;
        border: 4px solid rgba(255, 255, 255, 0.53);
        color: #fff;

        padding: 12px 16px;


        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
            padding: 10px;
        }
    }

    &__item-title {
        font-family: 'NotoSansMono';
        font-weight: 600;
        font-size: 60px;
        line-height: 125%;
        margin-right: 15px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            font-size: 50px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 42px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 35px;
        }
        
        span{
            margin-left: 10px;
        }
    }

    &__item-text {
        font-weight: 500;
    }

    &__bg{
        width: 360px;
        height: auto;
        object-fit: contain;
        position: absolute; 
        top: 0;
        left: 0;
        object-fit: contain;
        pointer-events: none;

        @media only screen and (max-width: $bp-lg) {
            top: auto;
            left: -150px;
            bottom: 160px;
        }

        @media only screen and (max-width: $bp-md) {
            bottom: 195px;
        }
    }
}


.analytics-list{
    max-width: 890px;
    display: block;
    column-count: 2;
    column-gap: 40px;
    margin-bottom: -25px;

    @media only screen and (max-width: $bp-xl) {
        max-width: none;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-bottom: -20px;
    }

    @media only screen and (max-width: $bp-md) {
        display: flex;
        flex-direction: column;
        padding-right: 50px;
    }


    &__item{
        break-inside: avoid;
        display: flex;
        margin-bottom: 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }

        img{
            width: 50px;
            height: 50px;
            flex-shrink: 0;
            margin-right: 15px;
        }

        p{
            font-family: 'NotoSansMono';
            font-size: 16px;
            line-height: 145%;
            margin: auto 0;
            color: #595959;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }
        }
    }
}

.analytics-item {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 600px;
    padding: 0 !important;
    color: $dark;

    @media only screen and (max-width: $bp-lg) {
        padding: 25px 0 !important;
    }

    &__inner {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
        }
    }

    &__content {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 445px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__icon {
        display: block;
        width: 50px;
        height: 50px;
        flex-shrink: 0;
        margin-bottom: 15px;
        filter: drop-shadow(0px 4px 22px rgba(48, 136, 226, 0.2));
    }

    &__title {
        margin-bottom: 10px;
    }

    &__text {
        margin-bottom: 15px;

        &:last-child{
            margin-bottom: 0;
        }

        span{
            color: $blue;
        }
    }

    &__img {
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        height: 100%;
        width: auto;
        max-width: 1440px;
        margin: 0 auto;

        @media only screen and (max-width: $bp-lg) {
            position: static;
            height: 352px;
            width: calc(100% + 40px);
            margin: 0 -20px;
        }
        
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: right center;

            @media only screen and (max-width: $bp-xl) {
                object-position: center;
            }
            
            @media only screen and (max-width: $bp-lg) {
                object-position: right center;
            }
        }
    }

    &--1 &__content{

    }

    &--2 {
        min-height: 568px;
    }

    &--2 &__content{
        width: 530px;
        margin-left: auto;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }

    }

    &--2 &__img{

        @media only screen and (max-width: $bp-lg) {
            height: 285px;
        }

        img{
            object-position: left center;
            
            @media only screen and (max-width: $bp-xl) {
                object-position: center;
            }

            @media only screen and (max-width: $bp-lg) {
                object-position: left center;
            }
        }
    }

    &--3 &__content{

    }
}
