@import "@theme/vars.scss";

button,
.button,
a {
    cursor: pointer;
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 34px;
    height: 55px;
    border-radius: 30px;
    font-family: 'NotoSansMono';
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 135%;
    background-color: $blue;
    color: #fff;
    fill: #fff;
    border: 1px solid #fff;
    box-shadow: 0px 4px 22px rgba(48, 136, 226, 0.63);
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease, box-shadow 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        height: 52px;
        font-size: 16px;
    }

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 24px;
        height: 24px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon + &__text{
        margin-left: 10px;
    }


    
    &:hover{
        box-shadow: 0px 4px 22px rgba(48, 136, 226, 0);
    }

    &--white{
        background-color: #fff;
        color: $blue;
        fill: $blue;
    }

    &[disabled]{
        pointer-events: none;
        opacity: 0.5;
    }


    // &--border{
    //     color: $yellow;
    //     fill: $yellow;
    //     background-color: transparent;

    //     &:hover{
    //         background-color: $yellow;
    //         border-color: $yellow;
    //         color: #fff;
    //         fill: #fff;
    //     }
    // }

    // &--border#{&}--white{
    //     border-color: #fff;
    //     fill: #fff;
    //     color: #fff;
    //     background-color: transparent;

    //     &:hover{
    //         border-color: #fff;
    //         background-color: #fff;
     
    //     }
    // }

    &--icon{
        padding: 0;
        width: 55px;
    }
}