@import "@theme/vars.scss";
@import "@theme/animations.scss";

.header {
    box-sizing: border-box;
    position: sticky;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    padding: 10px 0;
    transition: background-color 0.5s ease;
    background-color: #fff;

    @media only screen and (max-width: $bp-lg) {
        padding: 5px 0;
    }

    &.is-fixed{
    }

    &.is-active{
    }

    &__container {
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__burger-trigger {
        cursor: pointer;
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        flex-shrink: 0;
        width: 33px;
        height: 19px;
        display: none;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }

        &:hover {
            span,
            &::after,
            &::before {
                background-color: $blue-light;
            }
        }

        span,
        &::after,
        &::before {
            content: "";
            display: block;
            height: 3px;
            width: 33px;
            transform-origin: center;
            background-color: $blue;
            transition: transform 0.35s ease, opacity 0.35s ease, background-color 0.35s ease;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        width: 214px;
        height: 35px;

        @media only screen and (max-width: $bp-lg) {
            width: 128px;
            height: 20px;
            margin: 0 15px;
        }
        
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__actions {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media only screen and (max-width: $bp-lg) {

        }

        &>*{
            margin-bottom: 5px;

            &:first-child{
                margin-bottom: 0;
            }
        }
    }

    &__phone {
        font-family: 'DelaGothicOne';

        .icon{
            width: 30px;
            height: 30px;
            display: none;

            @media only screen and (max-width: $bp-lg) {
                display: flex;
            }
        }

        span{
            margin-left: 0 !important;
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }

    &__mail {
        box-sizing: border-box;
        cursor: pointer;
        font-weight: 400;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__burger {
        display: flex;
        flex-direction: column;
        z-index: 2;

        @media only screen and (max-width: $bp-lg) {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: #fff;
            transform: translateX(-100%);
            transition: transform 0.65s ease, box-shadow 0.25s ease;
        }
        
        &.is-active{
            transform: translateX(0);
        }
    }

    &__burger-inner {
    }

    &__burger-header {
        box-sizing: border-box;
        display: none;
        align-items: center;
        justify-content: space-between;
        padding: 12px 20px;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }
    }

    &__burger-logo {
        width: 128px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__burger-close {
        cursor: pointer;
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        fill: $blue;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue-light;
        }
    }

    &__burger-body {
        display: flex;
        flex-direction: column;
        // transition: transform 0.65s ease, opacity 0.65s ease;

        @media only screen and (max-width: $bp-lg) {
            padding: 45px 20px 30px;
            max-height: 100%;
            overflow-y: auto;
        }
    }

    &__nav {

        .nav {
            display: flex;
            align-items: center;


            @media only screen and (max-width: $bp-lg) {
                flex-direction: column;
                align-items: flex-start;
            }

            &__item {
                display: flex;
                text-decoration: none;
                color: $dark;
                margin: 0 15px;
                transition: color 0.35s ease;

                @media only screen and (max-width: $bp-lg) {
                    font-size: 18px;
                    line-height: 145%;
                    padding: 5px 0;
                    margin: 0 0 10px;

                    &:last-of-type{
                        margin-bottom: 0;
                    }
                }

                &:hover{
                    color: $blue;
                }
            }
        }
    }

    &__contacts{
        display: none;
        flex-direction: column;
        margin-top: 45px;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }
    }

    &__contact{
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        color: $dark;
        margin-bottom: 15px;
        transition: color 0.35s ease;

        .icon{
            width: 30px;
            height: 30px;
            margin-right: 10px;
        }

        &:hover{
            color: $blue;
        }
    }
}