@import '@theme/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}
.modal {
	&.fade &-dialog{
		transform: translateY(15px);
		transition: transform 0.25s ease-out;

	}

	&.show &-dialog{
		transform: none;
	}

	&-backdrop{
		&.show{
			background: #000;
			opacity: 0.45;
		}
	}

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 462px;
		margin-right: auto;
		margin-left: auto;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 20px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0 10px;
        }
	}

	&-content {
		box-sizing: border-box;
		border: none;
        background: linear-gradient(123.95deg, #307FE2 -0.82%, #0857BA 100%);
		border-radius: 10px;
        padding: 40px;
		border-radius: 30px;
		display: flex;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            padding: 32px;
            border-radius: 26px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 24px;
            border-radius: 22px;
        }
	}

	&-close {
		width: 24px;
		height: 24px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;
        margin-left: auto;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 15px;
        }

		.icon {
			width: 100%;
			height: 100%;
			transition: fill 0.35s ease;
			fill: #fff;
		}

		&:hover {
			.icon{
				fill: $blue-light;
			}
		}
	}

	&__main{
		display: flex;
		flex-direction: column;
	}

    &__header{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }
        
        &:last-child{
            margin-bottom: 0;
        }
    }

        
	&__title{
        font-family: 'DelaGothicOne';
        text-align: left;
        font-weight: 400;
        font-size: 25px;
        line-height: 145%;
        color: #fff;

        @media only screen and (max-width: $bp-lg) {
            font-size: 22px;
        }
    }

    &__subtitle{
        text-align: center;
        margin-top: 15px;
    }


	&__form{
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		form{
			display: flex;
			flex-direction: column;
			flex-grow: 1;
		}

        .input{
            margin-bottom: 15px;

            &:last-of-type{
                margin-bottom: 25px;
            }
        }

        .button{
            width: 100%;
        }
	}

    &__bg{
        position: absolute;
        width: 192px;
        height: 308px;
        top: 24px;
        right: 0;
        pointer-events: none;
        object-fit: contain;

        @media only screen and (max-width: $bp-lg) {
            top: 0;
            width: 155px;
            height: 250px;
        }
    }

    &--success &__title{
        text-align: center;
    }

    &--success &__bg{
        top: 12px;

        @media only screen and (max-width: $bp-lg) {
            top: -20px;
        }

    }

}