@import "@theme/vars.scss";

.footer {
    overflow: hidden;
    box-sizing: border-box;
    background: $dark;
    padding: 40px 0;

    @media only screen and (max-width: $bp-lg) {
        padding: 30px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 20px 0;
    }
    
    &__container {
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media only screen and (max-width: $bp-lg) {
            display: block;

            &::after{
                content: "";
                display: block;
                clear: both;
            }
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }

    }

    &__logo {
        display: flex;
        align-items: center;
        width: 214px;
        height: 35px;

        @media only screen and (max-width: $bp-lg) {
            width: 128px;
            height: 20px;
            float: left;
        }
        
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__nav {

        @media only screen and (max-width: $bp-lg) {
            float: right;
            display: flex;
            justify-content: flex-end;
            width: 50%;
        }
        
        @media only screen and (max-width: $bp-sm) {
            width: auto;
        }

        .nav {
            display: flex;
            align-items: center;

            @media only screen and (max-width: $bp-lg) {
                flex-direction: column;
                align-items: flex-start;
            }

            &__item {
                display: flex;
                text-decoration: none;
                color: #F0F0F0  ;
                margin: 0 15px;
                transition: color 0.35s ease;

                @media only screen and (max-width: $bp-lg) {
                    margin: 0 0 15px;
                    font-size: 14px;

                    &:last-of-type{
                        margin-bottom: 0;
                    }
                }

                &:hover{
                    color: $blue;
                }
            }
        }
    }

    &__actions {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        
        @media only screen and (max-width: $bp-lg) {
            float: left;
            width: 50%;
            margin-top: 20px;
        }

        @media only screen and (max-width: $bp-sm) {
            width: auto;
            width: 100%;
            max-width: 55%;
        }

        &>*{
            margin-bottom: 5px;

            &:first-child{
                margin-bottom: 0;
            }
        }
    }

    &__phone {
        font-family: 'DelaGothicOne';

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__mail {
        box-sizing: border-box;
        cursor: pointer;
        font-weight: 400;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 12px;
        }
    }

}