@import "@theme/vars.scss";

.autodiscovery {
    padding-top: 75px;

    @media only screen and (max-width: $bp-xl) {
        padding-top: 50px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding-top: 40px;
        padding-bottom: 0;
    }

    @media only screen and (max-width: $bp-md) {
        padding-top: 25px;
    }

    &__inner {
    }

    &__intro {
        margin-bottom: 100px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 75px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 50px;
        }
    }

    &__info {
        margin-bottom: 100px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 75px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 50px;
        }
    }

    &__demo {
    }
}

.autodiscovery-intro {
    color: #fff;

    &__inner{
        display: flex;
        flex-wrap: wrap;
    }

    &__title-wrapper {
        width: 100%;
        max-width: 642px;
        margin-right: 42px;

        @media only screen and (max-width: $bp-xxl) {
            margin-right: 30px;
        }

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
            margin-right: 0;
        }
    }

    &__subtitle {
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 0.39em;
        color: $blue;
        font-size: 18px;
        line-height: 145%;
        margin-bottom: 18px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }
    }

    &__title {

        @media only screen and (max-width: $bp-xl) {
            font-size: 26px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 22px;
        }
    }

    &__content {
        max-width: 520px;

        @media only screen and (max-width: $bp-xxl) {
            max-width: 428px;
        }

        @media only screen and (max-width: $bp-xl) {
            order: 1;
            max-width: none;
        }
        
        

        p{
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 20px;
            color: #F0F0F0;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 15px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-bottom: 10px;
            }

            &:last-of-type{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }

            span {
                font-weight: 700;
                color: $blue;
            }
        }
    }

    &__img {
        display: flex;
        max-width: 1410px;
        width: calc(100% + 170px);
        margin: 60px -85px 0;
        max-height: 650px;

        @media only screen and (max-width: $bp-xl) {
            margin: 30px -40px 30px;
            max-height: 600px;
            width: calc(100% + 80px);
        }

        @media only screen and (max-width: $bp-lg) {
            // height: 440px;
        }

        @media only screen and (max-width: $bp-md) {
            // height: 328px;
            height: auto;
            min-height: 328px;
            width: calc(100% + 40px);
            margin-right: -20px;
            margin-left: -20px;
        }

        img{
            max-width: 100%;
            height: 100%;
            object-fit: cover;

            @media only screen and (max-width: $bp-md) {
                object-position: 30%;
            }
        }
    }
}

.autodiscovery-info {
    color: #F0F0F0;

    &__inner {
        display: flex;
        flex-direction: column;
    }

    &__header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }

        p{
            display: inline-block;
            width: calc(50% - 10px);
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 15px;

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
                font-size: 14px;
                margin-bottom: 20px;
            }

            span{
                font-weight: 700;
                color: $blue;
            }
        }
    }

    &__body {

        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            align-items: flex-start;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__list {
        display: block;
        width: 572px;
        flex-shrink: 0;
        margin: 30px 40px 30px 0;

        @media only screen and (max-width: $bp-lg) {
            width: 460px;
            margin-right: 30px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin: 0;
        }

        ul{
            display: flex;
            flex-direction: column;
            padding: 0;
            margin: 0;

            li{
                display: flex;
                margin: 0 0 20px;
                padding: 0;

                p{
                    font-size: 14px;
                    line-height: 145%;

                    @media only screen and (max-width: $bp-lg) {
                        font-size: 12px;
                    }

                    span{
                        font-weight: 700;
                        color: $blue;
                    }
                }
            }
        }
    }

    &__img {
        display: flex;
        flex-shrink: 0;
        height: 600px;

        @media only screen and (max-width: $bp-xl) {
            height: 550px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 420px;
        }

        @media only screen and (max-width: $bp-md) {
            height: auto;
            max-height: 320px;
            width: calc(100% + 40px);
            margin: 0 -20px 30px;
            order: -1;
        }

        img{
            height: 100%;
            width: auto;
            object-fit: contain;

            @media only screen and (max-width: $bp-md) {
                width: 100%;
                height: auto;
            }
        }
    }
}

.autodiscovery-demo {
    color: #fff;

    &__inner {
        display: block;

        &::after{
            content: "";
            display: block;
            clear: both;
        }

        @media only screen and (max-width: $bp-lg) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        width: 450px;
        flex-shrink: 0;
        float: left;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;

        }

        p{
            font-weight: 600;
            font-size: 16px;
            line-height: 145%;
            color: #F0F0F0;
            margin-bottom: 15px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 10px;
            }

            &:last-of-type{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }

        }
    }

    &__subtitle{
        font-family: 'DelaGothicOne';
        font-weight: 400;
        margin-bottom: 20px;
    }

    &__interface {
        width: 50%;
        flex-shrink: 0;
        float: right;
        margin-left: 80px;

        @media only screen and (max-width: $bp-xxl) {
            margin-left: 50px;
        }

        @media only screen and (max-width: $bp-xl) {
            margin-left: 40px;
            width: calc(50% - 40px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-left: 0;
            max-width: 600px;
            margin-bottom: 30px;
        }
    }

    &__interface-title {
        color: $blue;
        margin-bottom: 45px;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        aspect-ratio: 1.63;
 
        @media only screen and (max-width: $bp-xxl) {
            
        }

        @media only screen and (max-width: $bp-lg) {
            aspect-ratio: 1.36;
        }

        img{
            width: 132%;
            height: auto;
            margin-right: -16%;
            margin-left: -17%;
            margin-bottom: -28%;
            margin-top: -22%;
            object-fit: contain;

            @media only screen and (max-width: $bp-lg) {
                width: 122%;
                margin-right: -8%;
                margin-left: -14%;
                margin-bottom: -20%;
                margin-top: -14%;
            }
        }
    }

    &__list {
        width: 450px;
        flex-shrink: 0;
        float: left;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }

        ul{
            display: flex;
            flex-direction: column;
            padding: 0;
            margin: 0;

            li{
                display: flex;
                padding: 0;
                margin: 0 0 20px;

                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 15px;
                }

                &:last-of-type{
                    margin-bottom: 0;
                }

                img{
                    display: flex;
                    width: 52px;
                    height: 52px;
                    flex-shrink: 0;
                    margin-right: 10px;
                    filter: drop-shadow(0px 4px 22px rgba(48, 136, 226, 0.2));
                }

                p{
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 145%;
                    margin: auto 0;
                }
            }
        }
    }
}