@import "@theme/vars.scss";

.intro {

    &__inner {
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: relative;
        z-index: 1;
        padding: 120px 0;
        min-height: 670px;

        @media only screen and (max-width: $bp-xl) {
            padding: 105px 0;
        }

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            justify-content: flex-start;
            min-height: 674px;
        }
    }

    &__main {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 610px;

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            max-width: 430px;
        }
    }

    &__title {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
            text-align: center;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 23px;
        }

        span{
            font-size: 55px;
            line-height: 125%;
            color: $blue;

            @media only screen and (max-width: $bp-xl) {
                font-size: 48px;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 43px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 28px;
            }
        }
    }

    &__subtitle {
        margin-bottom: 10px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            text-align: center;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }

        &:last-of-type{
            margin-bottom: 40px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 30px;
            }
        }
    }

    &__button {

        @media only screen and (max-width: $bp-lg) {
            padding: 0 50px;
        }
    }

    &__number {
        width: 310px;
        height: 88px;
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-110px, 50px);

        @media only screen and (max-width: $bp-xxl) {
            transform: translate(-90px, 40px);
        }

        @media only screen and (max-width: $bp-xl) {
            transform: translate(-50px, 30px);
        }

        @media only screen and (max-width: $bp-lg) {
            transform: translate(-30px, 15px);
            height: 66px;
            width: 232px;
        }
    }

    &__img {
        position: absolute;
        top: 50%;
        right: 0;
        width: 884px;
        height: auto;
        object-fit: contain;
        object-position: right center;
        transform: translate(144px, calc(-50% - 30px));

        @media only screen and (max-width: $bp-xxl) {
            width: 760px;
            transform: translate(124px, calc(-50% - 30px));
        }

        @media only screen and (max-width: $bp-xl) {
            width: 680px;
            transform: translate(84px, calc(-50% - 30px));
        }

        @media only screen and (max-width: $bp-lg) {
            top: auto;
            bottom: 0;
            right: 50%;
            transform: translateX(50%);
            width: auto;
            height: 300px;
            object-position: bottom center;
        }
    }
}