@import "@theme/vars.scss";

.link{
    display: inline-flex;
    align-items: center;
    color: $blue;
    fill: $blue;
    text-decoration: none;
    transition: fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

    &:hover{
        color: $blue-light;
        fill: $blue-light;
    }

    span{

    }

    .icon{
        width: 24px;
        height: 24px;
    }

    .icon + span {
        margin-left: 5px;
    }

    span + .icon{
        margin-left: 5px;
    }


    &-gray{
        color: #B0B0B0;
        fill: #B0B0B0;

        &:hover{
            color: $blue;
            fill: $blue;
        }
    }
}