@import "@theme/vars.scss";

.app{
    &__intro{
        margin-bottom: 65px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 40px;
        }
    }

    &__slider{

    }

    &__list {
    }

    &__item {
    }
}


.app-intro {

    &__inner {
        display: flex;
        align-items: center;
        position: relative;
        min-height: 350px;
        
        @media only screen and (max-width: $bp-md) {
            min-height: auto;
            flex-direction: column;
        }
    }

    &__content {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        width: 100%;
        max-width: 600px;
        
        @media only screen and (max-width: $bp-xl) {
            max-width: 475px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: none;
            margin-bottom: 20px;
        }

        p{
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 10px;

            &:last-of-type{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }
        }
    }

    &__icon {
        display: block; 
        width: 60px;
        height: 60px;
        flex-shrink: 0;
        margin-bottom: 15px;

        @media only screen and (max-width: $bp-lg) {
            width: 48px;
            height: 48px;
        }
    }

    &__title {
        background: linear-gradient(90deg, #00C6FB 0%, #005BEA 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 15px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 20px;
        }

    }

    &__img {
        pointer-events: none;
        display: flex;
        position: absolute;
        width: 435px;
        height: 525px;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -66px;
        right: 80px;

        img{
            width: 162%;
            object-fit: contain;

            @media only screen and (max-width: $bp-md) {
                width: 115%;
            }
        }

        @media only screen and (max-width: $bp-xxl) {
            right: 20px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 350px;
            top: -100px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 300px;
        }

        @media only screen and (max-width: $bp-md) {
            position: static;
            height: 330px;
            width: 100%;
            width: calc(100% + 40px);
            margin: 0 -20px;
            max-width: 390px;
        }
    }
}

.app-slider {

    &__inner {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-xl) {
            justify-content: center;
            flex-wrap: wrap;
        }
    }  

    &__thumb{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        p{
            font-size: 18px;
            line-height: 26px;
            text-align: center;
            color: #828282;
            transition:  font-size 0.35s ease, font-weight 0.35s ease,;

            @media only screen and (max-width: $bp-md) {
                font-size: 9px;
                line-height: 14px;
            }
        }
    }

    &__sliders{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
    }

    &__slider-thumbs{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 30px 24px;
        height: 335px;
        width: 244px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            width: 130px;
            height: 178px;
            padding: 16px 12px;
        }

        .slider {

            &__inner {
                position: static;
                height: 100%;
            }
        
            &__wrapper {
                position: static;
            }
        
            &__slide {
                cursor: pointer;
                min-height: 38px;
                height: max-content;
                transition: 0.35s ease;

                @media only screen and (max-width: $bp-md) {
                    min-height: 18px;
                }
                
                &.swiper-slide-thumb-active{
                    

                    .app-slider__thumb{
                        
                        p{
                            font-size: 22px;
                            font-weight: 700;
                            background: linear-gradient(89.18deg, #FF3D00 0.7%, #FFB800 99.3%);
                            -webkit-background-clip: text;
                            -webkit-text-fill-color: transparent;
                            background-clip: text;
                            text-fill-color: transparent;

                            @media only screen and (max-width: $bp-md) {
                                font-size: 12px;
                            }
                        }
                    }
                }
            }
        
            &__navigation {
                transform: none;
                flex-direction: column;
                position: absolute;
                top: 0;
                height: 100%;
            }
        
            &__button {
                width: 30px;
                height: 8px;
                fill: $blue-light;

                &:hover{
                    fill: $blue;
                }
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        }
    }

    &__slider{
        overflow: visible;
        box-sizing: border-box;
        width: 464px;
        height: 514px;
        padding: 38px 52px 0;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            width: 245px;
            height: 272px;
            padding: 18px 30px 40px;
        }

        .slider {

            &__inner {
                // border: 1px solid red;
                height: 400px;
                width: 100%;
                overflow: visible;

                @media only screen and (max-width: $bp-md) {
                    height: 280px;
                }
                
                @media only screen and (max-width: $bp-md) {
                    height: 214px;
                }
            }
        
            &__wrapper {
            }
        
            &__slide {
           
                .app-slider__item-label{
                    transform:  rotate(30deg);
                    opacity: 0;
                    transform-origin: left center;
                }

                &.swiper-slide-active{
                    transition-delay: 0.4s;

                    .app-slider__item-label{
                        transform: rotate(0);
                        opacity: 1;
                        transition-delay: 0.4s;
                    }
                }
            }
        
            &__navigation {
                width: calc(100% + 104px);
                left: -52px;
                right: -52px;

                @media only screen and (max-width: $bp-md) {
                    width: calc(100% + 56px);
                    left: -28px;
                    right: -28px;
                }
            }
        
            &__button {
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        }
    }

    &__slider-bg{
        pointer-events: none;
        display: block;
        position: absolute;
        top: -100px;
        right: -136px;
        width: 735px;
        height: 763px;

        @media only screen and (max-width: $bp-md) {
            width: 400px;
            height: 464px;
            top: -80px;
            right: -90px;
        }
    }
    
    &__slide-bg {
        pointer-events: none;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 336px;
        height: 400px;

        @media only screen and (max-width: $bp-md) {
            width: 178px;
            height: 212px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        height: 100%;
        box-sizing: border-box;
        padding: 58px 50px 72px 40px;

        @media only screen and (max-width: $bp-md) {
            padding: 32px 22px 35px;
        }


        ul{
            padding: 0;
            margin: 0;
            display: flex;
            flex-direction: column;
            max-height: 168px;
            overflow: hidden;

            @media only screen and (max-width: $bp-md) {
                max-height: 118px;
                
            }

            li{
                padding: 0;
                display: flex;
                margin-bottom: 10px;

                @media only screen and (max-width: $bp-md) {
                    margin-bottom: 5px;
                }
                
                &:last-of-type{
                    margin-bottom: 0;
                }

                &::before{
                    content: "";
                    display: inline-flex;
                    display: block;
                    width: 4px;
                    height: 4px;
                    border-radius: 10px;
                    flex-shrink: 0;
                    background-color: #fff;
                    margin: 8px 10px;

                    @media only screen and (max-width: $bp-md) {
                        margin: 5px 6px;
                        width: 2px;
                        height: 2px;
                    }
                }
            

                p{
                    color: #fff;
                    font-size: 14px;
                    line-height: 120%;
                    

                    @media only screen and (max-width: $bp-md) {
                        font-size: 9px;
                    }
                }
            }
        }


    }

    &__item-inner {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__item-subtitle {
        margin-top: auto;
        font-size: 14px;
        line-height: 120%;
        color: #fff;
        margin-left: 25px;
        max-width: 130px;
        
        @media only screen and (max-width: $bp-md) {
            margin-left: 15px;
            font-size: 8px;
            max-width: 72px;
        }
    }

    &__item-label {
        pointer-events: none;
        display: block;
        position: absolute;
        width: 210px;
        height: 210px;
        bottom: -60px;
        right: -32px;
        transition: opacity 0.5s ease, transform 0.75s ease, rotate 0.75s ease;
        

        @media only screen and (max-width: $bp-md) {
            width: 105px;
            height: 105px;
            bottom: -25px;
            right: -20px;
        }
    }


    &__info {
        display: flex;
        flex-direction: column;
        margin-left: 30px;
        max-width: 435px;

        @media only screen and (max-width: $bp-xl) {
            order: -1   ;
            width: 100%;
            max-width: 666px;
            margin-right: auto;
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
            margin-left: 0;
        }

        p{
            color: $dark;
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 20px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }
}

.app-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;

    @media only screen and (max-width: $bp-xl) {
        padding: 50px 0;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 30px 0;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 20px 0;
        flex-direction: column;
    }


    &__main {
        display: flex;
        flex-direction: column;
        max-width: 482px;

        @media only screen and (max-width: $bp-lg) {
            max-width: 460px;
            margin-right: 30px;
        }
        @media only screen and (max-width: $bp-md) {
            margin-bottom: 30px;
            margin-right: 0;
            max-width: none;
        }

        p{
            color: $dark;
            font-size: 16px;
            line-height: 145%;
            margin-bottom: 15px;

            &:last-of-type{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
                margin-bottom: 10px;
            }

            span{
                font-weight: 700;
                color: $blue;
            }
        }
    }

    &__title-wrapper {
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }

        img{
            width: 30px;
            height: 30px;
            object-fit: contain;
            flex-shrink: 0;
            margin-right: 15px;
        }
    }

    &__title {
        background: linear-gradient(90deg, #00C6FB 0%, #005BEA 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        @media only screen and (max-width: $bp-md) {
            font-size: 20px;
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 534px;
        height: 510px;

        @media only screen and (max-width: $bp-xl) {
            width: 380px;
            height: 363px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            width: 305px;
            height: 292px;
        }

        img{
            object-fit: contain;

        }
    }

    &:nth-of-type(1) &__img {
        height: 556px;

        @media only screen and (max-width: $bp-xl) {
            height: 392px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 315px;
        }

        img{
            width: 175%;
            margin-right: -42%;
            margin-left: -33%;
            margin-top: -35%;
            margin-bottom: -27%;
        }
    }
    
    &:nth-of-type(2) &__main{
        margin-right: 0;
    }

    &:nth-of-type(2) &__img {
        order: -1;
        margin-right: 30px;

        @media only screen and (max-width: $bp-md) {
            order: 0;
            margin-right: 0;
        }

        img{
            width: 167%;
            margin-right: -38%;
            margin-left: -29%;
            margin-top: -12%;
            margin-bottom: -39%;

        }
    }

    &:nth-of-type(3) &__img {
        
        img{
            width: 167%;
            margin-right: -38%;
            margin-left: -29%;
            margin-top: -12%;
            margin-bottom: -39%;

        }
    }
    
    &:nth-of-type(4) &__main{
        margin-right: 0;
    }

    &:nth-of-type(4) &__img{
        height: 552px;
        order: -1;
        margin-right: 30px;


        @media only screen and (max-width: $bp-xl) {
            height: 392px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 332px;
        }

        @media only screen and (max-width: $bp-md) {
            order: 0;
            margin-right: 0;
        }

        img{
            width: 170%;
            margin-right: -38%;
            margin-left: -28%;
            margin-top: -12%;
            margin-bottom: -32%;
        }
    }
}
