@import "@theme/vars.scss";

.section-form {
    .section{
        &__inner {
            padding: 0 20px;

            @media only screen and (max-width: $bp-xl) {
                padding: 0;
            }
        }
    }
    
}


.form-block {
    position: relative;
    display: flex;
    background: linear-gradient(136.09deg, #307FE2 -28.54%, #0857BA 100%);
    border: 2px solid #FFFFFF;
    box-shadow: 12px 13px 35px rgba(56, 124, 202, 0.3);
    border-radius: 15px;
    padding: 74px;
    overflow: hidden;

    @media only screen and (max-width: $bp-xl) {
        padding: 40px 56px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 24px 32px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 20px;
    }

    &__inner {
        width: 100%;
        position: relative;
        z-index: 1;
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 45px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 35px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 25px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__header-img {
        display: block;
        width: 74px;
        height: 74px;
        flex-shrink: 0;
        margin-right: 25px;
        object-fit: contain;

        @media only screen and (max-width: $bp-lg) {
            width: 56px;
            height: 56px;
            margin-right: 15px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 44px;
            height: 44px;
            margin-bottom: 15px;
        }
    }

    &__title {
        max-width: 490px;
        color: #fff;
    }

    &__body {
    }

    &__form {
        form{
            display: flex;
            flex-wrap: wrap;
    
            @media only screen and (max-width: $bp-lg) {
                max-width: 600px;
            }
            
            @media only screen and (max-width: $bp-md) {
                flex-direction: column;
                align-items: flex-start;
                max-width: 300px;
                max-width: none;
                width: 100%;
            }
    
            .input{
                width: auto;
                max-width: 276px;
                margin-right: 15px;
    
                @media only screen and (max-width: $bp-lg) {
                    width: 50%;
                    margin-bottom: 20px;
                }
    
                @media only screen and (max-width: $bp-md) {
                    width: 100%;
                    max-width: none;
                    margin-right: 0;
                    margin-bottom: 14px;
                }
    
                &:last-of-type{
                    margin-right: 20px;
    
                    @media only screen and (max-width: $bp-md) {
                        margin-right: 0;
                        margin-bottom: 20px;
                    }
                }
            }
    
            .button{
                @media only screen and (max-width: $bp-md) {
                    width: 100%;
                }
            }
        }
    }

    &__bg{
        pointer-events: none;
        position: absolute;
        object-fit: contain;
        object-position: right top;

        width: 616px;
        height: 480px;
        top: -5px;
        right: -185px;

        @media only screen and (max-width: $bp-xxl) {
            width: 520px;
            right: -165px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 480px;
            right: -150px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 267px;
            height: 207px;
            top: -15px;
            right: -112px;
        }
    }
}