// DelaGothicOne

@font-face {
    font-family: 'DelaGothicOne';
    src:    url('@fonts/DelaGothicOne-Regular.woff2') format('woff2'),
            url('@fonts/DelaGothicOne-Regular.woff') format('woff'),
            url('@fonts/DelaGothicOne-Regular.otf') format('otf'),
            url('@fonts/DelaGothicOne-Regular.ttf') format('ttf');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

